.users {
  margin: 2rem auto;
  width: 90%;
  max-width: 40rem;
}

.users ul {
  list-style: none;
  padding: 1rem;
}

.users li {
  display: flex;
  justify-content: space-between;
  border: 1px solid #ccc;
  margin: 0.5rem 0;
  padding: 0.5rem;
}

.users button{
    font: inherit;
    border: 1px solid #4f005f;
    background: #4f005f;
    color: white;
    padding: 0.25rem 1rem;
    cursor: pointer;
}

.users button:hover, .users button:active {
    background: #741188;
    border-color: #741188;
}
